.App {
  text-align: center;
  max-width: 720px;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  align-items: stretch;
  flex-direction: column;
}

.test {
  background: #fff;
  flex: 7;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.isValid {
  background-color: #2fd075;
}
.isInvalid {
  background-color: #b9282b;
}

.isInvalid .text, .isValid .text {
  color: #fff;
}

.answers {
  flex: 3;
}

.answer-btn {
 width: 50%;
 height: 50%;
 background-color: #c80148;
 font-size: 48px;
 font-weight: 800;
 color: #eee;
 box-shadow: none;
 border: 4px solid #eee;
 cursor: pointer;
}

.text {
  font-size: 78px;
  font-weight: 800;
  color: #c80148;
}